.navbar {
    max-height: 100%;
}
header.main>:last-child {
    margin: 0px;
    margin-bottom: 0.5em;
}
.page-template, .post-template {
    .featured-content {
        background: _palette(bg);
    }
    .no-featured {
        padding-top: 3em;
        padding-bottom: 3em;
    }
    .no-featured, .featured-content {
        h1, h2 {
            padding-top: 0.2em;
            margin-bottom: 20px;
        }
        p {
            margin-bottom: 20px;
        }
    }
}
.post-template {
    .date {
        display: block;
        text-align: right;
        color: _palette(fg-light);
    }
}